/*!
 * search - 0.1.0 */
.br-search {
  align-items: flex-end;
  display: flex;
  flex-wrap: wrap;
}

.br-search .br-input {
  flex: 1;
}

.br-search .feedback,
.br-search .help {
  order: 1;
  width: 100%;
}

.br-search .action {
  display: none;
}

@media (min-width: 576px) {
  .br-search .action {
    display: block;
  }
}
